<template>
	<view class="hc">
		<view class="bg" v-if="navshow">
			<view class="nav" @click="routeAbout()">
				<u-icon size="28" name="/static/img/3258.png" class="footIcon"></u-icon>
			</view>
			<span class="title">签约商家商品列表</span>
		</view>
		<view class="body">
			<view class="box">
				<v-prolist :plist="list"></v-prolist>
			</view>
		</view>
	</view>
</template>

<script>
	import proList from '@/components/prolist.vue'
	export default {
		components: {
			"v-prolist":proList
		},
		data() {
			return {
				navshow:false,
				list:[]
			}
		},
		methods: {
			routeAbout(){
				this.$u.route({
					type:'switchTab',
					url:'pages/home/home'
				})
			},
			//请求数据
			async loadData(){
				let list = await this.$api.json('goodsList');
				this.list = list;
			}
		},
		onLoad(){
			this.loadData();
		}
	}
</script>

<style lang="scss">
	page{background: #f0f0f0;}
	.hc {
		background: #f0f0f0;
		width: 100%;
		height: 100%;
		.bg {
			background: #FFFFFF;
			// background-color: red;
			display: flex;
			flex: 1;
			padding: 30rpx 50rpx;
			text-align: center;
			line-height: 100%;
			.nav{
				margin-right: 20rpx;
			}
			.title{
				font-size: 30rpx;
				font-weight: 550;
				color: #141313;
			}
		}
		.body{
			width: 100%;
			padding: 20rpx;
			.box{
				width: 100%;
				background-color: #FFFFFF;
				border-radius: 10rpx;
				padding:40rpx 50rpx;
			}
		}
	}
</style>
